/**
* @author William Cui
* @description 路由主界面顶层容器
* @date 2017-03-27
**/

import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router';
import { message } from 'antd';

import MorePopup from './app-popup/more-popup';//更多
import OpenClassPopup from './app-popup/open-class-popup';//开班
import Prompt from 'components/prompt-common';//错误提示

import Clock from '../components/clock';
import '../assets/styles/index/_fd_public.css';
import '../assets/styles/index/content.css';
import '../assets/styles/index/index_navs.css';
import '../assets/styles/index/index_top.css';
import '../assets/styles/index/left_bottom.css';
import '../assets/styles/index/ting_item.css';

import '../assets/styles/rblock/desk_info_container.css';
import '../assets/styles/rblock/desk_info_footer.css';

message.config({
	top: 300 
});

@inject('appStore') @observer
class App extends Component {

    constructor(props, context) {
        super(props, context);
        this.props.appStore.getMealName(0); 
        this.state={
            morePopup: false,
            openpopup: false,
        }
    }

    componentDidMount() {
        let timer = setInterval(() => {
            let unReadNumber = sessionStorage.getItem('unReadNumber') || false;
            this.setState({unReadNumber});
        },1000);
        this.setState({timer});
    }

    componentWillUnmount() {
        let { timer } = this.state;
        clearInterval(timer);
    }

    componentDidUpdate() {
        let appStore=this.props.appStore;  
        let feedback = appStore.feedback;
        if(feedback) {
        //提示
            switch(feedback.status) {
                case 'success':
                    message.success(feedback.msg,appStore.closeFeedback());
                    break;
                case 'warn':
                    message.warn(feedback.msg,appStore.closeFeedback());
                    break;
                case 'error':
                    message.warn(feedback.msg,appStore.closeFeedback());
                    break;
                default:
                    message.info(feedback.msg,appStore.closeFeedback());
            }
        }    
    }

    render() {
        let account = sessionStorage.getItem('account') ? JSON.parse(sessionStorage.getItem('account')) : {userName: ''};

        let { appStore } = this.props; 
        let feedback = appStore.feedback;
        let operatePrompt;
        if(feedback && feedback.status === 'error') {
            //错误提示
            feedback.okClick = () =>{
            if(feedback.confirmClick) feedback.confirmClick();
                feedback.cancelClick();
            }
            feedback.cancelClick = () =>{
                appStore.closeFeedback();
            }
            operatePrompt = <Prompt message={feedback} />
        }

        let unReadNumber = this.state.unReadNumber;  

        return <div id="nav">
            <div id="deskTop">
                <div className="top-l-block">
                    <span className="time"><Clock/></span>
                    <span>{appStore.mealName}</span>
                </div>
                <div className="top-c-block">厅面点菜 (发布时间) 2017-06-19 16:40</div>
                <div className="top-r-block">
                    <div className="top-point" onClick={() => {
                        //返回桌台界面
                        this.context.router.push('/message');
                    }}>
                        <i className="iconfont icon-home_title_notice_btn"></i>
                        {unReadNumber && <span>{unReadNumber}</span>}
                    </div>
                    <div>{account.userName}</div>
                    <div onClick={() => {
                        appStore.logout();
                    }}>注销</div>
                </div>
            </div>

            <div id="deskNav">
                <Link to='/dine' activeClassName='active'><i className="iconfont icon-home_title_restauran"></i>堂食</Link>
                <Link to='/member' activeClassName='active'><i className="iconfont icon-huiyuan"></i>会员</Link>
                <Link to='/order' activeClassName='active'><i className="iconfont icon-shangpin"></i>订单</Link>
                <Link to='/booking' activeClassName='active'><i className="iconfont icon-chajianleixing_yuding_"></i>预订</Link> 
                <a onClick={()=>{
                    this.setState({
                        openpopup: 
                        <OpenClassPopup 
                            closeCancel={()=>{
                                this.setState({openpopup:''}); 
                            }}
                            okCancel={()=>{
                                this.setState({openpopup:''}); 
                            }}
                        >
                        </OpenClassPopup>
                    });  
                    }}>
                    <i className="iconfont icon-shangban"></i>开班
                </a> 
                <Link to='/shift' activeClassName='active' ><i className="iconfont icon-jiaoban"></i>交班</Link> 
                
                <a onClick={()=>{
                    this.setState({
                        morePopup: <MorePopup closepopup={()=>{
                            this.setState({morePopup:''}); 
                        }} />
                    });
                    
                }}><i className="iconfont icon-gengduo"></i>更多</a>
            </div>
            {this.props.children}
            {this.state.morePopup}
            {this.state.openpopup}
        </div>
    }
}

App.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}

export default App;



