import React from 'react';
import { Modal } from 'antd';
import { observer, inject } from 'mobx-react';
import { Link } from 'react-router'; 
import Promptpopup from 'components/prompt-popup';//提示
import { browserHistory } from 'react-router'; //路径跳转　
import '../modal.css';
import './more_popup.less';   
 
//更多入口弹窗
@inject('selloutStore','dayEndStore') @observer
class MorePopup extends React.Component {

  constructor(props, context) {
    super(props, context);   
    this.state={tolink: '',Promptpopup:'' };
  } 
  
  componentDidMount() {
     //沽清判断/sellout
    let selloutStore=this.props.selloutStore;
    selloutStore.getTakeeffect(); 

    //上一营业日有数据但没日结时
     let dayEndStore=this.props.dayEndStore;
     dayEndStore.checkBeforeDailyWorking();
  }

  //关闭按钮事件
  handleCancel=()=>{ 
    if(this.props.closepopup){
      this.props.closepopup();
    }
  }

  //日结
  dayend=()=>{ 
    let dayEndStore=this.props.dayEndStore;
    if(dayEndStore.checkBefore){
          this.setState({Promptpopup:<Promptpopup cancelText={"知道了"}　okText={"去日结"} 
                        onCancel={()=>{//知道了
                            this.setState({Promptpopup:''}); 
                        }}
                        onOk={()=>{//去日结 
                            this.setState({Promptpopup:'',today:true});
                            browserHistory.push('/day-end'); //去到日结页面
                        }}>
                            <div className="prompt" > 
                                <div className="delele-text"> 
                                    上一营业日还没日志
                                </div> 
                            </div>
                        </Promptpopup>})
    }else{
       browserHistory.push('/day-end'); //去到日结页面
    }
    

  }

  render() {   
    let selloutStore=this.props.selloutStore; 
     let dayEndStore=this.props.dayEndStore;
    return (
       <div>
          <Modal 
            title="更多" 
            visible={true}   
            onCancel={this.handleCancel} 
            footer={null} 
            width={840}
            wrapClassName="index-popup-modal"
          >  
            <div className="popup-center">
                  <Link className="pages-div" to={selloutStore.Takeeffect ? '/sellout/after' : '/sellout' }>沽清</Link> 
                  <Link className="pages-div" to="/discontinued">停售</Link>
                  <Link className="pages-div" to="/price-maintenance">时价维护</Link>
                  <Link className="pages-div" to="check-in">签到</Link>
                  <div className="pages-div" onClick={this.dayend}>日结</div>
                  <Link className="pages-div" to="/responsible">桌台负责人</Link>
            </div>
            <div className="Prompt_button" onClick={this.handleCancel}>关闭</div>
          </Modal>
          {this.state.Promptpopup}
       </div>
    );
  }
}

MorePopup.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}

export default MorePopup;


